/**
Hover styling for all devices and browsers
 */
@mixin hover() {
	@media (hover: none) {
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);

		&:active {
			@content;
		}
	}

	@media (hover: hover),
	all and (-ms-high-contrast: none),
	(-ms-high-contrast: active) {
		&:hover {
			@content;
		}
	}
}